.list {
  color: white;
  width: 300px;
  background-color: #bf07ac;
  height: 50px;
  border-radius: 10px;
  margin: 20px auto;
}

.list p {
  padding: 10px;
  position: relative;
}

.list p span {
  position: absolute;
  right: 10px;
  cursor: pointer;
}
.list p span:hover {
  color: rgb(255, 0, 0);
}

.faicons {
  margin: 0 5px;
  font-size: 17px;
}

.list p input {
  background-color: transparent;
  border: 0;
  color: #fff;
  font-size: 18px;
}
.list p input:focus {
  outline: none;
}
